<template>
  <div class="mx-auto flex flex-wrap mt-4 items-center max-w-xs px-3">
    <label v-if="!inLobby" class="w-full mr-auto ml-auto mt-4 mb-4">
      <span class="text-white">Speltype:</span>
      <select v-model="type" class="form-select mt-1 block w-full">
        <option>Lokaal</option>
        <option>Online</option>
      </select>
    </label>
    <local-form v-if="type === 'Lokaal'" @start="startGame"></local-form> 
    <online-form v-if="type === 'Online'" @in-lobby="toggleLobby" @start-game="startOnlineGame"></online-form> 
  </div>
</template>

<script>
import LocalForm from "./LocalForm.vue";
import OnlineForm from "./OnlineForm.vue";
import { mapActions } from 'vuex';

export default {
  components: {
    LocalForm,
    OnlineForm
  },
  emits: ["start-game"],
  data() {
    return {
      gameData: [],
      type: 'Online',
      inLobby: false
    };
  },
  methods: {
    ...mapActions(['setGameData', 'setGameType']),
    startGame(data) {
      data.players.forEach((i) => {
        let name = data.names[i] ? data.names[i] : `Speler ${i + 1}`;
        this.gameData.push({
          name: name,
          red: [],
          yellow: [],
          blue: [],
          fail: [],
          total: [],
        });
      });
      // push gameData to Vuex store
      this.setGameData({ gameData: this.gameData });
      this.setGameType({ type: this.type} );
      // Tell App.vue that gameData was set and start game
      this.$emit("start-game");
    },
    startOnlineGame(data) {
      data.forEach((player) => {
        this.gameData.push({
          name: player.username,
          red: [],
          yellow: [],
          blue: [],
          fail: [],
          total: [],
        });
      });
      // push gameData to Vuex store
      this.setGameData({ gameData: this.gameData });
      this.setGameType({ type: this.type} );

      // Tell App.vue that gameData was set and start game
      this.$emit("start-game");
    },
    toggleLobby() {
      this.inLobby = true;
    }
  }
};
</script>