<template>
  <form v-if="!inLobby" @submit.prevent="findGame" class="w-full mb-6 md:mb-0">
    <div>
      <label class="text-white">Naam:</label>
      <input
        type="text"
        class="w-full border px-4 py-2 rounded focus:border-blue-500 focus:shadow-outline outline-none autofocus"
        autocomplete="off"
        placeholder="Gebruikersnaam"
        v-model="username"
        maxlength="25"
        autofocus
        required
      />
    </div>
    <div class="text-center mt-4">
      <base-button type="submit" mode="hover:opacity-75">Zoek spel</base-button>
    </div>
  </form>
  <game-lobby :username="username" v-if="inLobby" @start-game="startGame"></game-lobby>
</template>

<script>
import GameLobby from "./GameLobby.vue";

export default {
  emits: ['in-lobby', 'start-game'],
  components: {
    GameLobby
  },  
  data() {
    return {
      username: '',
      inLobby: false
    };
  },
  methods: {
    findGame() {
      if (this.username !== '') {
        this.inLobby = true;
        this.$emit('in-lobby');
      }
    },
    startGame(players) {
      this.$emit('start-game', players)
    }
  }
};
</script>
