<template>
  <div @click="addScore($event, color)" class="flex flex-wrap mb-2 sm:ml-5 sm:mr-5" :class="`${color}-row`">
    <slot></slot>
  </div>  
  <base-dialog :show="!!errorMessage" type="error" title="Foutmelding" @close="errorMessage = null">
    <template #default>
      <p>{{ errorMessage }}</p>
    </template>
    <template #actions>
      <base-button @click="errorMessage = null">OK</base-button>
    </template>
  </base-dialog>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  props: {
    color: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      errorMessage: null
    };
  },
  methods: {
    ...mapActions(['setScore', 'nextPlayer', 'setGameOver']),
    addScore(e, color) {
      if (this.myName !== this.gameData[this.roundData.activePlayer].name && this.myName !== null) {
        return;
      }
      let field = parseInt(e.target.classList[1].slice(1), 10)
      if (this.roundData.dices[color] > 0 && 
      e.target.tagName === 'P' && 
      !this.gameData[this.roundData.activePlayer][color][field] && 
      !this.roundData.scoreAdded[this.roundData.activePlayer]) {
        // Check if score can be added
        const check = this.scoreCheck(field, color);

        // Only run if score can be added
        if (check === true) {
          this.setScore({ 
            color: color, 
            field: field 
          });
          // Check if winning conditions are met
          this.calcWinningConditions();
          this.nextPlayer({ next: null });
        } else if (check === 'already in column') {
          this.errorMessage = 'Het getal mag niet hetzelfde zijn als een getal hier onder of boven.';
        } else if (check === 'not ascending') {
          this.errorMessage =  'De getallen moeten oplopend zijn.';
        }

        // Determine error
      } else if (this.roundData.remainingRolls === 2) {
        this.errorMessage = 'Je moet eerst gooien.';
      } else if (this.roundData.dices[color] === 0 && !this.roundData[this.roundData.activePlayer]) {
        this.errorMessage = 'Deze kleur is niet gebruikt.';
      }
    },
    scoreCheck(field, color) {
      let player = this.roundData.activePlayer;
      let score = this.roundData.dices.total;
      // Copy score array with the current score in it
      let copyArray = [...this.gameData[player][color]];
      copyArray[field] = score;
      // Create new array without empty elements
      let filteredArray = copyArray.filter(n => n);
      // Check if array is ascending
      if (this.isSorted(filteredArray)) {
        // Check if number is not the same as field above or below
        if (color === 'red' && (score === this.gameData[player].yellow[field] || score === this.gameData[player].blue[field])) {
          return 'already in column';
        } else if (color === 'yellow' && (score === this.gameData[player].red[field] || score === this.gameData[player].blue[field])) {
          return 'already in column';
        } else if (color === 'blue' && (score === this.gameData[player].red[field] || score === this.gameData[player].yellow[field])) {
          return 'already in column';
        } 
        return true;
      } 
      return 'not ascending';
    },
    isSorted(arr) {
      const limit = arr.length - 1;
      return arr.every((_, i) => (i < limit ? arr[i] < arr[i + 1] : true));
    },
    calcWinningConditions() {
      for (let i = 0; i < this.numPlayers; i++) {
        let fullRows = 0;
        // Create array with every row color
        let colorArray = ['red', 'yellow', 'blue'];
        colorArray.forEach( color => {
          let filteredArray = this.gameData[i][color].filter(n => n);
          // Increment fullRows if row has 9 elements
          if (filteredArray.length === 9) {
            fullRows++;
          }
        });
        if (fullRows >= 2) {
          this.setGameOver();
          this.socket.emit('gameOver');
        }
      }
    }
  },
  computed: {
    ...mapGetters(['gameData', 'roundData', 'numPlayers', 'myName'])
  }
};
</script>