<template>
  <div v-if="showPlayer" class="flex justify-center mt-3">
    <div :class="`player-${player}-panel`" class="w-screen max-w-xl shadow-xl rounded bg-gray-200">
      <!-- <div class="flex justify-center items-center">
        <button @click="navPlayers('prev')" class="h-6 w-6 text-sm mr-3 flex justify-center items-center rounded-full bg-gray-400">
            <i class="fa fa-chevron-left" aria-hidden="true"></i>
        </button> -->
        <div 
          :class="{'text-green-600':winner.includes(player)}" 
          class="text-2xl font-bold text-center my-4" 
          :id="`name-${player}`">
            {{ gameData[player].name }}
            <span v-if="winner.includes(player)"> wint het spel!</span>
        </div>
        <!-- <button @click="navPlayers('next')" class="h-6 w-6 text-sm ml-3 flex justify-center items-center rounded-full bg-gray-400">
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
        </button>
      </div>  --> 
      <div :class="`player-${player}-score-panels`">
        <score-row color="red">
          <score-field 
            v-for="(fields, i) in scoreFieldData.red" 
            :key="i"
            :player="player"
            :type="fields"
            :field="i"
            color="red"
          ></score-field>
        </score-row>
        <score-row color="yellow">
          <score-field 
            v-for="(fields, i) in scoreFieldData.yellow" 
            :key="i"
            :player="player"
            :type="fields"
            :field="i"
            color="yellow"
          ></score-field>
        </score-row>
        <score-row color="blue">
          <score-field 
            v-for="(fields, i) in scoreFieldData.blue" 
            :key="i"
            :player="player"
            :type="fields"
            :field="i"
            color="blue"
          ></score-field>
        </score-row>
        <failed-row :player="player"></failed-row>  
        <total-row :player="player" :gameOver="gameOver" @calc-winner="$emit('calc-winner')"></total-row>  
      </div>    
    </div>   
  </div>
</template>

<script>
import ScoreRow from "./ScoreRow.vue";
import ScoreField from "./ScoreField.vue";
import FailedRow from "./FailedRow.vue";
import TotalRow from "./TotalRow.vue";
import { mapGetters } from 'vuex';

export default {
  props: {
    player: Number,
    gameOver: Boolean,
    winner: Array
  },
  emits: ['calc-winner'],
  components: {
    ScoreRow,
    ScoreField,
    FailedRow,
    TotalRow
  },
  data() {
    return {
      scoreFieldData: {
        red: ['blank', 'blank', 'normal', 'bonus', 'normal', 'empty', 'normal', 'bonus', 'normal', 'normal', 'normal', 'normal'],
        yellow: ['blank', 'normal', 'normal', 'normal', 'normal', 'normal', 'empty', 'normal', 'bonus', 'normal', 'normal', 'blank'],
        blue: ['normal', 'normal', 'bonus', 'normal', 'empty', 'normal', 'normal', 'normal', 'normal', 'bonus', 'blank', 'blank'],
      },
      viewingPlayer: this.player
    };
  },
  methods: {
    navPlayers(nav) {
      if (nav === 'prev') {
        if (this.viewingPlayer === 0) {
          this.viewingPlayer = this.numPlayers - 1;
        } else {
          this.viewingPlayer--
        }
        console.log(this.viewingPlayer);
      } else {
        if (this.viewingPlayer === this.numPlayers - 1) {
          this.viewingPlayer = 0;
        } else {
          this.viewingPlayer++
        }
        console.log(this.viewingPlayer);
      }
    }
  },
  computed: {
    ...mapGetters(['gameData', 'roundData', 'numPlayers']),
    showPlayer() {
      return this.roundData.activePlayer === this.player || this.gameOver
    }
  }
};
</script>