<template>
  <div @click="clickFail" class="flex flex-wrap justify-center mt-4 mb-4 sm:ml-5 sm:mr-5">
    <p class="w-full mt-1 mr-2 text-xs text-center">Mislukte worp: -5</p>
    <div 
      v-for="i in [0, 1, 2, 3]"
      :key="i"
      class="w-1/12 bg-gray-400" 
      :class="{ 'rounded-l-lg': i === 0, 'rounded-r-lg': i === 3 }">
      <p class="w-8 h-8 pt-1 mx-1 my-1 text-center bg-white rounded-md">{{ isFail(i) }}</p>
    </div>
  </div>
  <base-dialog :show="confirm" type="confirm" title="Weet je het zeker?" @close="confirmFail(false)">
    <template #default>
      <p>In dit geval krijg je -5 punten en gaat de beurt naar de volgende speler.</p>
    </template>
    <template #actions>
      <base-button class="mr-2" mode="hover:opacity-75" @click="confirmFail(true)">OK</base-button>
      <base-button mode="hover:opacity-75" @click="confirmFail(false)">Nee</base-button>
    </template>
  </base-dialog>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  props: ['player'],
  data() {
    return {
      confirm: false
    };
  },
  computed: {
    ...mapGetters(['gameData', 'roundData', 'socket'])
  },
  methods: {
    ...mapActions(['setScore', 'nextPlayer', 'setGameOver']),
    isFail(field) {
      if (this.gameData[this.player]['fail'][field] === -5) return 'X';
    },
    clickFail() {
       // Player fails round
      if (this.roundData.remainingRolls < 2 && this.roundData.scoreAdded.length === 0) {
        this.confirm = true;
      }
    },
    confirmFail(answer) {
      this.confirm = false;
      if (answer) {
        this.setScore({ 
          color: 'fail', 
          field: this.gameData[this.roundData.activePlayer].fail.length 
        });
        this.checkNumFails();
        this.nextPlayer({ next: null });
      } 
    },
    checkNumFails() {
      if (this.gameData[this.roundData.activePlayer].fail.length === 4) {
        this.setGameOver();    
        this.socket.emit('gameOver');
      }
    }
  }
};
</script>