<template>
  <div class="mt-5 text-center">
    <h2 class="font-bold text-white">Dobbelstenen:</h2>
    <div :class="{ 'opacity-50':roundData.remainingRolls < 2 }" class="checkboxes">
      <input 
        :disabled="disableInput" 
        type="checkbox" 
        class="w-8 h-8 text-red-600 bg-red-600 mr-1 form-checkbox" 
        value="red" 
        v-model="checkedColors" 
        autocomplete="off" 
        checked
      >
      <input 
        :disabled="disableInput" 
        type="checkbox" 
        class="w-8 h-8 text-yellow-400 bg-yellow-400 form-checkbox" 
        value="yellow" 
        v-model="checkedColors" 
        autocomplete="off" 
        checked
      >
      <input 
        :disabled="disableInput" 
        type="checkbox" 
        class="w-8 h-8 text-blue-800 bg-blue-800 ml-1 form-checkbox" 
        value="blue" 
        v-model="checkedColors" 
        autocomplete="off" 
        checked
      >
    </div>
    <div class="mt-4">
      <base-button 
        @click="rollDices" 
        :disabled="disableButton" 
        :mode="{'hover:opacity-75':!disableButton}">
        <i class="fas fa-redo"></i> Gooien
      </base-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  data() {
    return {
      checkedColors: ['red', 'yellow', 'blue'],
    };
  },
  computed: {
    ...mapGetters(['roundData']),
    disableButton() {
      return this.checkedColors.length === 0 || this.roundData.remainingRolls === 0;
    },
    disableInput() {
      return this.roundData.remainingRolls < 2;
    }
  },
  methods: {
    ...mapActions(['resetDiceNums', 'setDice', 'setDiceNum', 'updateRemainingRolls', 'setTotal']),
    rollDices() {
      // Set all dices numbers to null
      this.resetDiceNums()
      // Call calcDices for each element in checkedColors
      this.checkedColors.forEach(e => this.calcDices(e, 1, 6));
      // Update remaining rolls
      this.updateRemainingRolls();
      // Add dices and push total to Vuex store
      let total = this.roundData.dices.red + this.roundData.dices.yellow + this.roundData.dices.blue;
      this.setTotal({ total: total });
    },
    calcDices(color, min, max) {
      // Reset previous dice
      this.setDice({ color: color, num: 0 });
      // Calc random dice number
      const randomNum = Math.floor(Math.random() * (max - min + 1)) + min;
      this.setDice({ color: color, num: randomNum });
      // Convert numbers to text for FontAwesome
      this.convertNum(color);
    },
    convertNum(color) {
      switch (this.roundData.dices[color]) {
        case 1:
          this.setDiceNum({ color: color, val: 'one' });
          break;
        case 2:
          this.setDiceNum({ color: color, val: 'two' });
          break;
        case 3:
          this.setDiceNum({ color: color, val: 'three' });  
          break;
        case 4:
          this.setDiceNum({ color: color, val: 'four' }); 
          break;
        case 5:
          this.setDiceNum({ color: color, val: 'five' }); 
          break;
        case 6:
          this.setDiceNum({ color: color, val: 'six' }); 
          break;
      }
    }
  }
};
</script>