<template>
  <teleport to="#app">
    <p class="loading text-center text-3xl text-white">
      Meer spelers zoeken
      <span>.</span>
      <span>.</span>
      <span>.</span>
    </p>
    <div v-if="timer" class="text-white text-center">
      <p class="text-m mt-1">Spel begint over:</p>
      <p class="loading text-xl">{{ time }}</p>
    </div>
    <p v-if="!timer" class="loading text-center text-xs text-white">Het spel begint als er 2 of meer spelers zijn.</p>
    <div class="mx-auto mt-4 items-center max-w-xs p-3 text-center bg-gray-300 rounded">
      <h1 class="text-lg font-bold">Gebruikers:</h1>
      <ul>
        <li 
          v-for="(user, i) in users" 
          :key="user.id" 
          :class="{ 'text-orange-600 font-bold': user.username === username }">
          {{i + 1}}. {{ user.username }}
        </li>
      </ul>
    </div>
  </teleport>
  <base-dialog :show="error" type="error" title="Foutmelding" @close="handleError">
    <template #default>
      <p>{{ error }}</p>
    </template>
    <template #actions>
      <base-button @click="handleError">OK</base-button>
    </template>
  </base-dialog>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  props: ['username'],
  emits: ['start-game'],
  data() {
    return {
      users: [],
      error: false,
      time: null,
      timer: null
    };
  },
  methods: {
    ...mapActions(['setName']),
    handleError() {
      this.error = false;
      location.href = '/';
    },
    startTimer() {
      this.timer = setInterval( () => {
        if(this.time <= 0){
          clearInterval(this.timer);
          this.setName({ name: this.username });
          this.$emit('start-game', this.users);
        } 
        this.time -= 1;
      }, 1000);
    } 
  },
  computed: {
    ...mapGetters(['socket']),
    numPlayers() {
      return this.users.length;
    }
  },
  watch: {
    numPlayers() {
      if (this.numPlayers >= 2) {
        this.time = 10;
        this.startTimer();
      }
      if (this.numPlayers < 2) {
        clearInterval(this.timer);
        this.timer = null;
      }
    }
  },
  created() {
    this.socket.on('roomData', ({ room, users }) => {
      console.log(room);
      this.users = users;
    });

    if (this.username) {
      this.socket.emit('join', { username: this.username, room: '1' }, error => {
        if (error) {
          this.error = error;
        }
      });
    }
  },
};
</script>

<style>
@keyframes blink {
  0% {
    opacity: 0.2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 0.2;
  }
}

.loading span {
  animation-name: blink;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

.loading span:nth-child(2) {
  animation-delay: 0.2s;
}

.loading span:nth-child(3) {
  animation-delay: 0.4s;
}
</style>
