import { createStore } from 'vuex';
import io from 'socket.io-client';

import mutations from './mutations.js';
import actions from './actions.js';
import getters from './getters.js';

const store = createStore({
  state() {
    return {
      gameType: null,
      socket: io(),
      gameData: [],
      myName: null,
      roundData: {},
      gameOver: false
    };
  },
  mutations,
  actions,
  getters
});

export default store;