<template>
  <div class="flex justify-center mt-3">
    <div class="flex flex-wrap justify-center h-auto container">
      <div class="w-screen max-w-md">
        <div class="bg-gray-200 border-t-4 rounded-b border-gray-500 px-4 py-3 shadow-md" role="alert">
          <p class="font-bold text-sm">Beschikbare acties:</p>
          <ul v-if="myName === gameData[roundData.activePlayer].name || !myName" class="list-disc text-xs ml-4">
            <li v-if="roundData.remainingRolls === 2">Kies met welke dobbelstenen je wilt gooien.</li>
            <li v-if="roundData.remainingRolls > 0 && !scoreAdded">Gooi dobbelstenen ({{ throws }}).</li>
            <li v-if="roundData.remainingRolls < 2 && !scoreAdded">Vul score ({{ roundData.dices.total }}) in.</li>
            <li v-if="roundData.remainingRolls < 2 && roundData.scoreAdded.length === 0">Kruis vakje bij mislukte worp aan.</li>
            <li v-if="roundData.scoreAdded[roundData.activePlayer]">...</li>
          </ul>
          <ul v-if="(myName !== gameData[roundData.activePlayer].name) && gameType !== 'Lokaal'" class="list-disc text-xs ml-4">
            <li>Wacht tot {{ gameData[roundData.activePlayer].name }} klaar is met zijn beurt.</li>
          </ul>
          <p v-if="roundData.scoreAdded.length > 0 && !roundData.scoreAdded[roundData.activePlayer] && (myName === gameData[roundData.activePlayer].name || !myName)">
            <a @click="nextPlayer({ instant: true })" class="text-xs text-blue-500 underline hover:text-blue-700 skip" href="#">Sla over →</a>
          </p>
        </div>
      </div>
    </div>    
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  methods: {
    ...mapActions(['nextPlayer']),
  },
  computed: {
    ...mapGetters(['gameData', 'roundData', 'myName', 'gameType']),
    throws() {
      if (this.roundData.remainingRolls === 2) return '2 resterende worpen';
      return '1 resterende worp';
    },
    scoreAdded() {
      return this.roundData.scoreAdded[this.roundData.activePlayer]
    }
  }
};
</script>