<template>
  <p class="w-full mt-1 mr-2 text-xs text-center">Totaal</p>
  <div class="flex flex-wrap justify-center bg-gray-500 rounded-b">
    <div class="w-1/12 rounded-l-lg">
      <p class="w-8 h-8 pt-1 mx-1 my-1 text-center bg-white border-2 border-red-600 rounded-md">{{ gameData[player].total[0] }}</p>
    </div>
    <div class="w-1/12 rounded-l-lg">
      <p class="w-8 h-8 pt-1 mx-1 my-1 text-center bg-white border-2 border-yellow-400 rounded-md">{{ gameData[player].total[1] }}</p>
    </div>
    <div class="w-1/12 rounded-l-lg">
      <p class="w-8 h-8 pt-1 mx-1 my-1 text-center bg-white border-2 border-blue-800 rounded-md">{{ gameData[player].total[2] }}</p>
    </div>
    <p class="mt-1 text-lg font-bold text-white xl:mr-1">+</p>
    <div class="w-1/12">
      <p class="score-bonus">{{ gameData[player].total[3] }}</p>
    </div>
    <div class="w-1/12">
      <p class="score-bonus">{{ gameData[player].total[4] }}</p>
    </div>
    <div class="w-1/12">
      <p class="score-bonus">{{ gameData[player].total[5] }}</p>
    </div>
    <div class="w-1/12">
      <p class="score-bonus">{{ gameData[player].total[6] }}</p>
    </div>
    <div class="w-1/12">
      <p class="score-bonus">{{ gameData[player].total[7] }}</p>
    </div>
    <p class="mt-1 text-lg font-bold text-white xl:mr-1">-</p>
    <div class="w-1/12 rounded-l-lg">
      <p class="w-8 h-8 pt-1 mx-1 my-1 text-center bg-white rounded-md">{{ gameData[player].total[8] }}</p>
    </div>
    <p class="mt-1 text-lg font-bold text-white xl:mr-1">=</p>
    <div class="w-1/12 rounded-l-lg">
      <p class="w-12 h-8 pt-1 mx-1 my-1 text-center bg-white border-2 border-gray-800 rounded-md">{{ gameData[player].total[9] }}</p>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  props: {
    player: Number,
    gameOver: Boolean
  },
  emits: ['calc-winner'],
  data() {
    return {
      totalScores: []
    };
  },
  methods: {
    ...mapActions(['setTotalScore']),
    calcBonus(player, fieldNo, bonusField, otherField1, otherField2) {
      if (this.gameData[player][bonusField][fieldNo] > 0 && this.gameData[player][otherField1][fieldNo] > 0 && this.gameData[player][otherField2][fieldNo] > 0) {
        return this.gameData[player][bonusField][fieldNo];
      } 
      return 0;
    }
  },
  computed: {
    ...mapGetters(['gameData'])
  },
  watch: {
    gameOver() {
      this.gameData.forEach((_, player) => {
        // Create new array for storing totals row
        let total = [];
        // For every color: if array has 9 elements, take last value as score; else take the number of elements as score
        let colorArray = ['red', 'yellow', 'blue'];
        colorArray.forEach(color => {
          let newArray = this.gameData[player][color].filter(n => n);
          if (newArray.length === 9) {
            total.push(newArray[8]);
          } else {
            total.push(newArray.length);
          }
        });
        total.push(
          this.calcBonus(player, 3, 'red', 'yellow', 'blue'), 
          this.calcBonus(player, 7, 'red', 'yellow', 'blue'),
          this.calcBonus(player, 8, 'yellow', 'red', 'blue'),
          this.calcBonus(player, 2, 'blue', 'red', 'yellow'),
          this.calcBonus(player, 9, 'blue', 'red', 'yellow')
        );
        // -5 points for every failed round
        let failPoints = 0;
        this.gameData[player].fail.forEach(element => {
          failPoints += element;
        });
        total.push(failPoints);
        // Add up everything and store it as the last element of total array
        total.push(total.reduce((a, b) => a + b, 0));
        this.setTotalScore({ player: player, total: total });
        this.$emit('calc-winner');
      });
    }
  }
};
</script>