<template>
  <the-header></the-header>
  <start-form v-if="numPlayers === 0" @start-game="startGame"></start-form>
  <score-card 
    v-for="(player, i) in gameData" 
    :key="i" 
    :player="i"
    :game-over="gameOver"
    :winner="winner"
    @calc-winner="calcWinner"
  ></score-card>
  <available-actions v-if="checkActivePlayer && !gameOver"></available-actions>
  <control-panel v-if="checkActivePlayer && !gameOver && (myName === gameData[roundData.activePlayer].name || !myName)">
  </control-panel>
  <the-dices></the-dices>
  <base-dialog :show="!!error" type="error" title="Foutmelding" @close="handleError">
    <template #default>
      <p>{{ error }}</p>
    </template>
    <template #actions>
      <base-button @click="handleError">OK</base-button>
    </template>
  </base-dialog>
</template>

<script>
import TheHeader from "./components/TheHeader.vue";
import StartForm from "./components/StartForm.vue";
import ScoreCard from "./components/ScoreCard.vue";
import ControlPanel from "./components/ControlPanel.vue";
import TheDices from "./components/TheDices.vue";
import AvailableActions from "./components/AvailableActions.vue";
import { mapActions, mapGetters } from 'vuex';

export default {
  name: "App",
  components: {
    TheHeader,
    StartForm,
    ScoreCard,
    ControlPanel,
    TheDices,
    AvailableActions
  },
  data() {
    return {
      winner: [],
      error: false
    };
  },
  computed: {
    ...mapGetters(['gameType', 'gameData', 'roundData', 'gameIsOver', 'numPlayers', 'myName', 'socket']),
    checkActivePlayer() {
      return typeof this.roundData.activePlayer !== 'undefined';
    },
    gameOver() { 
      let filteredArray;
      if (this.roundData.scoreAdded) {
        filteredArray = this.roundData.scoreAdded.filter(n => n);
      }
      return this.gameIsOver && filteredArray.length === this.numPlayers;
    },
    activePlayer() {
      return this.roundData.activePlayer;
    }
  },
  methods: {
    ...mapActions(['resetRoundData', 'setRoundData', 'setGameData', 'setGameOver']),
    startGame() {
      this.resetRoundData();
    },
    calcWinner() {
      // Create new array with winning scores
      let totalScores = [];
      this.gameData.forEach((player) => {
        totalScores.push(player.total[9]);
      });
      const max = Math.max(...totalScores);
      totalScores.forEach((item, index) => item === max ? this.winner.push(index) : null);
    },
    handleError() {
      this.error = false;
      location.href = '/';
    }
  },
  watch: {
    numPlayers() {
      if (this.numPlayers < 2) {
        this.error = 'Er zijn niet genoeg spelers om verder te gaan met het spel. Probeer opnieuw een spel te vinden.'
      }
    },
    /* activePlayer() {
      if 
    } */
  },
  created() {
    this.socket.on('connect', () => {
      console.log('Connected!');
    });

    this.socket.on('roundData', (data) => {
      this.setRoundData(data);
    });

    this.socket.on('gameData', (data) => {
      this.setGameData({ gameData: data });
    });

    this.socket.on('gameOver', () => {
      this.setGameOver();
    });
  }  
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: #181b20;
}
</style>
